<template>
    <div class="find">
          <!-- 宠物展示 -->
        <div class="popularMenu">
          <!-- <div class="popularMenu_title">猫科</div> -->
          <!-- <div class="popularMenu_title">
            <div class="popularMenu_title_item">{{ShowStyleName}}</div>
          </div> -->

          <div class="popularMenu_liatItem">
            <div class="clearfix">
              <div class="popularMenu_imgData fl" v-for="(item,index) in showCollectionData" :key="index" @click="goDetail(item.name,item)">
                <div class="popularMenu_img">
                  <img v-lazy="item.coverURL" />
                </div>
                <div>
                  <div class="popularMenu_imgData_title">
                      <div class="popM_imgD_tItem on-title">{{item.name}}</div>
                       <div class="pets_link" @click.stop="myLike(index,item)">
                         <van-icon
                          name="delete"
                          color="#1989fa"
                          class="item_watch_icon"
                          size="24"
                        />
                       
                      </div>
                  </div>
                  <div class="on-title">{{item.desc}}</div>
                </div>
              </div>
            </div>
          </div>
        </div>
    </div>
</template>

<script>
import '../../assets/css/Find.less'
import { createNamespacedHelpers } from "vuex";

const { mapState, mapMutations } = createNamespacedHelpers("homeModules");
    export default {
        data(){
            return {
                showCollectionData: null
            }
        },
        created(){
           let localSData=localStorage.getItem('pestData');
            localSData=JSON.parse(localSData);
          if(localSData!=null){
            this.showCollectionData=localSData['myLikeData'];
          }else{
            this.showCollectionData=this.myLikeData;
          }
            
        },
        computed:{
            ...mapState(['myLikeData'])
        },
        methods: {
          ...mapMutations(['removemyLikeData','changeLike']),
          goDetail(name,item){
             this.$router.push({ name: "Detail", query: { name, type: item.listType,styleName: item.styleTitle} });
          },
          myLike(index,item){
            if(item.styleTitle=="mySearchData"){
            
              this.changeLike({type:item.styleTitle,num:item.listType,isflag: true,pestNick: item.name});
                        
            }
            else{
                 this.changeLike({type: item.styleTitle,num: item.listType,listNum: item.saveAddNum,isflag: true,pestNick: item.name});
            }

             this.removemyLikeData(index);
             let localSData=localStorage.getItem('pestData');
             localSData=JSON.parse(localSData);
            this.showCollectionData=localSData['myLikeData'];
          }
        }
    }
</script>

<style lang="less" scoped>

</style>